 <template>
  <div class="home-box">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- 提示框 -->
      <van-notice-bar
        mode="closeable"
        left-icon="volume-o"
        scrollable
        background="#CD2525"
        color="#ffffff"
      >618大狂欢，京东购物，更快、更省！</van-notice-bar>
      <!-- 搜索框 -->
      <van-search
        shape="round"
        background="#AE0907"
        placeholder="请输入搜索关键词"
        disabled
        show-action
        label="JD"
      >
        <template #left>
          <van-icon name="wap-nav" size=".9rem" color="#ffffff" />
        </template>
        <template #action>
          <div class="login" @touchstart="SkipLogin">登录</div>
        </template>
      </van-search>
      <!-- 轮播图 -->
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item>
          <van-image
            fit="contain"
            src="//m.360buyimg.com/mobilecms/s700x280_jfs/t1/113715/26/10432/84575/5eeb6578E970f37d1/8f587b425d0afddd.jpg!q70.jpg.dpg"
          />
        </van-swipe-item>
        <van-swipe-item>
          <van-image
            fit="contain"
            src="//m.360buyimg.com/mobilecms/s700x280_jfs/t1/112127/35/8751/101036/5ed37cd5E79a8753c/8ef4aaf67a3b8a3e.jpg!cr_1125x445_0_171!q70.jpg.dpg"
          />
        </van-swipe-item>
        <van-swipe-item>
          <van-image
            fit="contain"
            src="//m.360buyimg.com/mobilecms/s700x280_jfs/t1/110828/13/12868/84949/5ee71b38Eab1c17aa/92e6aac0af2dc139.jpg!cr_1125x445_0_171!q70.jpg.dpg"
          />
        </van-swipe-item>
        <van-swipe-item>
          <van-image
            fit="contain"
            src="//imgcps.jd.com/ling4/100002427783/56m66LCD5paw5qy-6YCf6YCS/5Lq65rCU55av5oqi5Zeo57-75aSp/p-5c131e9282acdd181da66199/f09d4a6e/cr_1125x445_0_171/s1125x690/q70.jpg"
          />
        </van-swipe-item>
      </van-swipe>
      <!-- 分割线 -->
      <van-divider>
        <template>
          <div class="hot">为你推荐</div>
        </template>
      </van-divider>
      <!-- 列表 -->
      <van-list
        v-model="loading"
        offset="50"
        @load="onLoad"
        :finished="finished"
        :immediate-check='false'
        finished-text="没有更多了"
      >
        <div v-for="i in len" :key="i">
          <Good :good ='list[2*(i-1)]'></Good>
          <Good :good ='list[2*(i-1)+1]'></Good>
        </div>
      </van-list>
    </van-pull-refresh>

    <!-- 底部tab -->
    <TabBar></TabBar>
  </div>
</template>

<script>
// 抛出
import {
  NoticeBar,
  Search,
  Icon,
  Swipe,
  SwipeItem,
  Image,
  Divider,
  List,
  PullRefresh,
  Toast
} from "vant";
export default {
  name: "Home",
  data: function() {
    return {
      list:[],
      isLoading:false,
      page: 1,
      finished: false,
      loading: false
    };
  },
  // 计算属性
  computed: {
    len: function() {
      return Math.floor(this.list.length/2)
    }
  },
  mounted() {
    this.getHotGoodList()
    this.isLoading=true
  },
  methods: {
    // 跳转页面
    SkipLogin() {
      console.log(111);
      this.$router.push("/Login"); //向后跳
      // this.$router.replace("/Login"); //替代当前路由
    },
    // 上滑加载
    onLoad() {
      this.loading = true
      this.page++
      this.getHotGoodList()
    },
    // 下拉刷新
    onRefresh(){
      this.page=1
      this.getHotGoodList()
    },
    // 获取列表
    getHotGoodList(){
      let params={
        hot:false,
        // page:this.page,
      }
      this.$http.getHotGoodList(params).then(res=>{
        if(res.length < 10){
          Toast("已经加载完了")
        }
        if(this.isLoading){
          this.list= res
        }else{
          this.list=[...this.list,...res]//下拉加载拼接
        }
        this.finished = false
        this.isLoading = false
        this.loading = false
      })
    }
  },
  //组件
  components: {
    TabBar: () => import("@/components/commen/TabBar.vue"),
    Good: () => import("@/components/commen/Good.vue"),
    [NoticeBar.name]: NoticeBar,
    [Search.name]: Search,
    [Icon.name]: Icon,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image,
    [Divider.name]: Divider,
    [List.name]: List,
    [PullRefresh.name]:PullRefresh
  }
};
</script>

<style lang='scss' scoped>
.home-box {
  .login {
    color: #ffffff;
  }
  .my-swipe {
    padding: 0;
    .van-swipe-item {
      text-align: center;
      .van-image {
        display: block;
        width: 10rem;
      }
    }
  }
  .van-divider {
    border-color: #b20d07;
    .hot {
      color: #b20d07;
      font-size: 0.4rem;
      font-weight: bold;
    }
  }
}
</style>

